import React from 'react';
import { Box, Grid, Paper } from '@mui/material';
import Divider from '@mui/material/Divider';
import MobileDemo from '../../../Components/Sidework/BridgeCareVideo';

export default function LandingScreen() {
  return (
    <Box paddingTop={15} height="90vh">
      <Divider variant="fullWidth" color="#C5C6B6" />
      <Grid container justifyContent="center" alignItems="center" spacing={2} height="100%">
        <Grid item xs={12} textAlign="center">
          <Paper elevation={0} sx={{ fontSize: '4rem', color: '#4D4CAB' }}>
            Mobile Demo
          </Paper>
        </Grid>
        <Grid item xs={12} textAlign="center">
          <Paper elevation={0} sx={{ fontSize: '1.25rem', color: '#2D2D2D', fontWeight: '300', padding: '2rem' }}>
            This project was crafted using After Effects, Illustrator, and Photoshop,
            employing special effects such as particle world, colorama, looping expressions, and additional techniques.
            Its purpose is to highlight a mobile application designed to mentor and aid international students in overcoming various challenges.
          </Paper>
        </Grid>
        <Grid item xs={12} textAlign="center">
          <MobileDemo />
        </Grid>
      </Grid>
    </Box>
  );
}
