import * as React from 'react';
import Timeline from '@mui/lab/Timeline';
import TimelineItem from '@mui/lab/TimelineItem';
import TimelineSeparator from '@mui/lab/TimelineSeparator';
import TimelineConnector from '@mui/lab/TimelineConnector';
import TimelineContent from '@mui/lab/TimelineContent';
import TimelineOppositeContent from '@mui/lab/TimelineOppositeContent';
import TimelineDot from '@mui/lab/TimelineDot';
import Typography from '@mui/material/Typography';
import SchoolOutlinedIcon from '@mui/icons-material/SchoolOutlined';
import WorkOutlineOutlinedIcon from '@mui/icons-material/WorkOutlineOutlined';
import SportsMotorsportsOutlinedIcon from '@mui/icons-material/SportsMotorsportsOutlined';
import WorkspacePremiumOutlinedIcon from '@mui/icons-material/WorkspacePremiumOutlined';

export default function CustomTimeline(){
  return (
    <Timeline position="alternate">
      <TimelineItem>
        <TimelineOppositeContent
          sx={{ m: 'auto 0', paddingBottom:'2rem' }}
          align="right"
          variant="body1"
          color="text.primary">
          October 2021
        </TimelineOppositeContent>
        <TimelineSeparator >
            <TimelineDot sx={{ bgcolor: 'primary.light'}}>
              <WorkspacePremiumOutlinedIcon />
            </TimelineDot>
          <TimelineConnector />
        </TimelineSeparator>
        <TimelineContent sx={{ py: '0rem', px: 2}}>
          <Typography variant="h5" component="span">Coursera: Google UX Course</Typography>
          <Typography>Completed in March 2022, I attained an average grade of 96% across seven courses encompassing dynamic websites, wireframing, design processes, prototypes, and UX Research.</Typography>
        </TimelineContent>
      </TimelineItem>
    <TimelineItem>
      <TimelineOppositeContent
        sx={{ m: 'auto 0'}}
        variant="body1"
        color="text.primary">
          January 2022
      </TimelineOppositeContent>
        <TimelineSeparator>
          <TimelineConnector />
            <TimelineDot sx={{ bgcolor: 'warning.main'}}>
            <SportsMotorsportsOutlinedIcon />
          </TimelineDot>
          <TimelineConnector />
        </TimelineSeparator>
        <TimelineContent sx={{ py: '2rem', px: 2 }}>
          <Typography variant="h5" component="span">Dallas Formula Racing</Typography>
          <Typography>I held a leadership position in marketing where I oversaw social media accounts, coordinated events, and fostered a stronger connection between the student organization and the student body.</Typography>
        </TimelineContent>
      </TimelineItem>
      <TimelineItem>
      <TimelineOppositeContent
        sx={{ m: 'auto 0' }}
        variant="body1"
        color="text.primary">
          March 2022
      </TimelineOppositeContent>
        <TimelineSeparator>
          <TimelineConnector />
          <TimelineDot sx={{ bgcolor: 'success.light'}}>
            <WorkOutlineOutlinedIcon />
          </TimelineDot>
          <TimelineConnector />
        </TimelineSeparator>
        <TimelineContent sx={{ py: '0px', px: 2 }}>
          <Typography variant="h5" component="span">
            Gulf Coast Technologies
          </Typography>
          <Typography>I completed contracted work that provided valuable experience in delivering style sheets and user flows to clients for both new applications and the redesign of web-based platforms.</Typography>
        </TimelineContent>
      </TimelineItem>
      <TimelineItem>
      <TimelineOppositeContent
        sx={{ m: 'auto 0' }}
        variant="body1"
        color="text.primary">
          June 2023
      </TimelineOppositeContent>
        <TimelineSeparator>
          <TimelineConnector />
          <TimelineDot sx={{bgcolor: 'success.light'}}>
            <WorkOutlineOutlinedIcon />
          </TimelineDot>
          <TimelineConnector />
        </TimelineSeparator>
        <TimelineContent sx={{ py: '12px', px: 2 }}>
          <Typography variant="h5" component="span">
          Verity Instruments Internship
          </Typography>
          <Typography>I completed a UX Design Internship where I initiated and completed the design process for an application focused on advancing optical endpoint detection methods within the Semiconductor Industry.</Typography>
        </TimelineContent>
      </TimelineItem>
      <TimelineItem>
        <TimelineOppositeContent
          sx={{ m: 'auto 0' }}
          variant="body1"
          color="text.primary">
            December 2023
        </TimelineOppositeContent>
        <TimelineSeparator>
          <TimelineConnector />
            <TimelineDot sx={{bgcolor: 'warning.main'}}>
              <SchoolOutlinedIcon />
          </TimelineDot>
          <TimelineConnector />
        </TimelineSeparator>
        <TimelineContent sx={{ py: '12px', px: 2 }}>
          <Typography variant="h5" component="span">
            Bachelors Degree from University of Texas at Dallas
          </Typography>
          <Typography>Completed Bachelor's degree in the school of Arts, Technology, and Emerging Communications. I developed skills in design, UX research, game development, and digital art.</Typography>
        </TimelineContent>
      </TimelineItem>
      <TimelineItem>
        <TimelineOppositeContent
          sx={{ m: 'auto 0' }}
          variant="body1"
          color="text.primary">
            January 2024
        </TimelineOppositeContent>
        <TimelineSeparator>
          <TimelineConnector />
            <TimelineDot sx={{bgcolor: 'success.light'}}>
              <WorkOutlineOutlinedIcon />
          </TimelineDot>
        </TimelineSeparator>
        <TimelineContent sx={{ py: '12px', px: 2 }}>
          <Typography variant="h5" component="span">
            Bachelors Degree from University of Texas at Dallas
          </Typography>
          <Typography>Completed Bachelor's degree in the school of Arts, Technology, and Emerging Communications. I developed skills in design, UX research, game development, and digital art.</Typography>
        </TimelineContent>
      </TimelineItem>
    </Timeline>
  );
}