import React from 'react';
import { Box, Grid, Paper } from '@mui/material';
import Divider from '@mui/material/Divider';

export default function UserResearch (){
    return(
        <Box >
            <Divider color="#C5C6B6" variant='middle' />
            <Grid container direction="row" alignContent="stretch" xs={12} padding={10}>
                <Grid xs={8}>
                    <Grid container direction="column" justifyContent="space-between" alignContent="stretch">
                        <Paper elevation={0} sx={{fontSize:'4rem', color:'#343232'}}>
                            Usability Testing
                        </Paper>
                        <Paper elevation={0} sx={{fontSize:'1.25rem', color:'#2D2D2D', fontWeight:'300', padding:'5rem'}}>
                        The usability studies conducted in two rounds revealed the need for efficient schedule creation, improved communication capabilities, and better tracking of truck locations, as well as the importance of enhancing navigation cues and accessibility features such as iconography and text scale in the manager's interface design for food truck operations.
                        </Paper>
                    </Grid>
                </Grid>
            </Grid>
        </Box>
    )
}