import React from 'react';
import { Box, Container, Grid, Paper } from '@mui/material';
import WellsPharm1 from '../../../Graphics/Walter White Expanded.png'
import Divider from '@mui/material/Divider';

export default function LandingScreen (){
    return(
        <Box paddingTop={10} paddingBottom={10}>
            <Container>
            <Divider color="#C5C6B6" variant="fullWidth" />
            </Container>
            <Grid container direction="row" alignContent="stretch" xs={12} padding={10}>
                <Grid xs={6}>
                    <Grid container direction="column" justifyContent="space-between" alignContent="stretch">
                        <Paper elevation={0} sx={{fontSize:'4rem', color:'#343232'}}>
                            UI Structure
                        </Paper>
                        <Paper elevation={0} sx={{fontSize:'1.25rem', color:'#2D2D2D', fontWeight:'300', padding:'2rem'}}>
                        The screens were redesigned with a modern and clean interface, incorporating Wells Pharmacy's color scheme and iconography. 
                        Functionality was enhanced, particularly focusing on refilling and filling prescriptions, which were lacking in the original platform. 
                        Rail navigation was designed for ease of use, and mock-ups were created using Figma to visualize the new design. 
                        Overall, the project aimed to create a user-friendly and efficient platform for Wells Pharmacy and physicians.
                        </Paper>
                    </Grid>
                </Grid>
                <Grid xs={6}>
                    <Paper elevation={0}>
                        <img
                            src={WellsPharm1}
                            alt='Wells Pharmacy Laptop'
                            width='100%'
                            style={{borderRadius: 10}}
                        ></img>
                    </Paper>
                </Grid>
            </Grid>
        </Box>
    )
}