import React from 'react';
import { Box, Grid, Paper } from '@mui/material';
import DigitalWireFrames from '../../../Graphics/WireFrames.png'
import Divider from '@mui/material/Divider';

export default function PaperWireFrames (){
    return(
        <Box >
            <Divider color="#C5C6B6" variant='middle' />
            <Grid container direction="row" alignContent="stretch" xs={12} padding={10}>
            <Paper elevation={0} sx={{fontSize:'4rem', color:'#343232'}}>
                            Digital Wire Frames
                        </Paper>
                        <Paper elevation={0}>
                            <img
                                src={DigitalWireFrames}
                                alt='Digital Wire Frames'
                                width='100%'
                            >
                            </img>
                        </Paper>
                <Grid xs={8}>
                    <Grid container direction="column" justifyContent="space-between" alignContent="stretch">
                        <Paper elevation={0} sx={{fontSize:'1.25rem', color:'#2D2D2D', fontWeight:'300', padding:'5rem'}}>
                        The digital wireframes for the manager's interface incorporate several key features to enhance usability and functionality:
                        </Paper>
                        <Paper elevation={0} sx={{fontSize:'1.25rem', color:'#2D2D2D', fontWeight:'300', padding:'1rem'}}>
                                Date Visibility and Selection: The current schedule is prominently displayed, allowing users to easily select the desired date and navigate to the specific week they are looking for. A "filter by week" feature enables quick access to specific time periods.
                        </Paper>
                        <Paper elevation={0} sx={{fontSize:'1.25rem', color:'#2D2D2D', fontWeight:'300', padding:'1rem'}}>
                                Filtering Options: A row of buttons at the top provides filtering options, allowing users to filter schedules by day, week, or year, depending on their needs.
                        </Paper>
                        <Paper elevation={0} sx={{fontSize:'1.25rem', color:'#2D2D2D', fontWeight:'300', padding:'1rem'}}>
                                New Schedule Creation: A high-contrast button positioned at the bottom right corner offers quick access for creating a new schedule. This feature streamlines the process for managers to initiate and manage schedules efficiently.
                        </Paper>
                        <Paper elevation={0} sx={{fontSize:'1.25rem', color:'#2D2D2D', fontWeight:'300', padding:'1rem'}}>
                                Single-Day Layout: The interface defaults to a single-day layout, displaying the current day's schedule by default. It provides a comprehensive 24-hour view of employee assignments and shifts, enabling managers to oversee operations effectively.
                        </Paper>
                        <Paper elevation={0} sx={{fontSize:'1.25rem', color:'#2D2D2D', fontWeight:'300', padding:'5rem'}}>
                            These features collectively aim to optimize the manager's workflow by providing intuitive navigation, efficient scheduling capabilities, and clear visibility of pertinent information.
                        </Paper>
                    </Grid>
                </Grid>
            </Grid>
        </Box>
    )
}