import * as React from 'react';
import Card from '@mui/material/Card';
import CardContent from '@mui/material/CardContent';
import CardMedia from '@mui/material/CardMedia';
import Typography from '@mui/material/Typography';
import { CardActionArea, CardActions } from '@mui/material';
import Image from '../../Graphics/NineSquares.webp';
import OpenVideo from './NineSquaresVideo';

const cardStyle = {
  position: 'relative', // Make the card container relative
  maxWidth: 345,
  height: '100%', // Ensure the card takes up the entire height
};

const buttonStyle = {
  position: 'absolute', // Position the button absolutely within the card container
  bottom: 2, // Adjust the distance from the bottom
  left: 2, // Align the button to the left side
};

export default function ProjectCards() {
  return (
    <Card elevation={4} sx={{ maxWidth: '345px', minHeight:'450px', margin:'1.5rem' }}>
      <div style={cardStyle}>
        <CardActionArea disableRipple sx={{ cursor: "default" }}>
          <CardMedia
            component="img"
            height="160"
            image={Image}
            alt="Nine Squares Thumbnail"
          />
          <CardContent>
            <Typography gutterBottom variant="h5" component="div">
              Nine Squares: Adobe After Effects Motion Graphics II
            </Typography>
            <Typography variant="body2" color="text.secondary">
              In this Adobe After Effects project for a Motion Graphics class, I crafted a series of short clips within a 3 by 3 grid, focusing on highlighting nine out of 12 animation principles.
            </Typography>
          </CardContent>
        </CardActionArea>
        <CardActions style={buttonStyle}>
          <OpenVideo />
        </CardActions>
      </div>
    </Card>
  );
}
