import * as React from 'react';
import { styled } from '@mui/material/styles';
import Box from '@mui/material/Box';
import Paper from '@mui/material/Paper';
import Grid from '@mui/material/Grid';
import Profile from '../../Graphics/Preston.webp';
import { Typography } from '@mui/material';
import animationData from '../../Jethro.json';
import Lottie from 'react-lottie';

const Item = styled(Paper)(({ theme }) => ({
  backgroundColor: theme.palette.mode === 'dark' ? '#1A2027' : '#fff',
  ...theme.typography.body2,
  padding: theme.spacing(1),
  textAlign: 'center',
  color: theme.palette.text.secondary,
}));

const Img = styled('img')({
  margin: 'auto',
  display: 'block',
  maxWidth: '100%',
  maxHeight: '100%',
});

const defaultOptions = {
  loop: true,
  autoplay: true,
  animationData: animationData,
  rendererSettings: {
    preserveAspectRatio: "xMidYMid slice"
  }
};

export default function AboutSection() {
  return (
    <Box sx={{ flexGrow: 1}}>
      <Item elevation={4}>
        <Grid container spacing={4} alignItems="center">
          <Grid item xs={12} sm={3}>
            <Img
              src={Profile}
              alt="Preston Profile Picture"
              style={{
                borderRadius: 400,
              }}
            />
          </Grid>
          <Grid item xs={12} sm={9}>
            <Box sx={{ display: 'flex', flexDirection: 'column' }}>
              <Typography variant="h5" sx={{ color: '#2D2D2D', padding:'.5rem'}}>I'm Preston Wolfe and this is my React MUI portfolio!</Typography>
              <Typography variant="h6" sx={{ color: '#2D2D2D', marginBottom: '1rem' }}>Objective</Typography>
              <Typography variant="body1" sx={{marginBottom:'2.5rem'}}>
                I am a recent graduate from the University of Texas at Dallas with a Bachelor's degree in Arts, Technology, and Emerging Communications (ATEC), specializing in UX Design. Possessing a background in programming and application development, I am eager to expand my understanding and exposure to user experience. Seeking opportunities to apply my newfound skills to engaging projects that challenge and inspire growth in the field.
              </Typography>
            </Box>
          </Grid>
          <Grid container
                direction="row"
                justifyContent="flex-start" 
                item 
                xs={6}
                sx={{ position: 'relative', 
                      marginTop:'2rem',
                      marginLeft:'2rem'
                }}>
                  
            <Box style={{ position: 'absolute', bottom: 120, left: -40, maxHeight:'5vh', maxWidth:'20vw'}}>
              <Lottie 
                options={defaultOptions}
                width={'10rem'}
                height={'100%'}
                animationData={animationData}
              />
            </Box>
            <Box>
              <Typography sx={{fontSize:'.5rem'}}>Custom made with Lottie from Airbnb</Typography>
            </Box>
          </Grid>
        </Grid>
      </Item>
    </Box>
  );
}