import React from 'react';
import { Box, Grid, Paper } from '@mui/material';
import Divider from '@mui/material/Divider';
import WireFrame1 from '../../../Graphics/Homescreen 1.png'
import WireFrame2 from '../../../Graphics/Homescreen2.png'
import WireFrame3 from '../../../Graphics/Homescreen3.png'
import WireFrame4 from '../../../Graphics/HomeScreenFinal.png'
import ScheduleWireframe from '../../../Graphics/SchedulingWireframe.png'

export default function LandingScreen (){
    return(
        <Box paddingTop={15}>
            <Divider variant="fullWidth" color="#C5C6B6"/>
            <Grid>
                <Paper elevation={0} sx={{fontSize:'4rem', color:'#7B7AC1'}}>
                    WireFrames
                </Paper>
                <Paper elevation={0} sx={{fontSize:'1.25rem', color:'#2D2D2D', fontWeight:'300', padding:'2rem'}}>
                    When I joined this start up I jumped in on the first day and started designs for an app that could help international students through coaching and counseling.
                    Getting ideas out quick and receiving feedback I quickly adjust to the direction my team was seeing for the future of this application. AI chat.
                </Paper>
                <Grid container direction='row' justifyContent='space-between' alignContent='center' xs={12}>
                    <Paper elevation={0} xs={4}>
                        <img
                        src={WireFrame1}
                        alt='WireFrame Homescreen 1'
                        style={{maxWidth:'15rem'}}
                        >
                        </img>
                    </Paper>
                    <Paper elevation={0} xs={4}>
                        <img
                        src={WireFrame2}
                        alt='WireFrame Homescreen 2'
                        style={{maxWidth:'15rem'}}
                        >
                        </img>
                    </Paper>
                    <Paper elevation={0} xs={4}>
                        <img
                        src={WireFrame3}
                        alt='WireFrame Homescreen 3'
                        style={{maxWidth:'15rem'}}
                        >
                        </img>
                    </Paper>
                </Grid>
                <Grid container direction='row' justifyContent='space-between' alignItems='center' paddingTop={20}>
                    <Grid item xs={6} container direction='column' justifyContent='space-between' alignItems='center'>
                        <Grid item xs={1} elevation={0} sx={{ fontSize: '2rem', color: '#2D2D2D', textAlign: 'center' }}>
                        Refined
                        </Grid>
                        <Grid item xs={1} elevation={0} sx={{ fontSize: '1.25rem', color: '#2D2D2D', fontWeight: '300', padding: '2rem' }}>
                        Focusing on this first round of feedback, the approach to the homescreen was recentered towards an interactive chat.
                        </Grid>
                    </Grid>
                     <Grid item xs={4} elevation={0} >
                        <img
                            src={WireFrame4}
                            alt='Refined Wireframe Homescreen'
                            style={{ maxWidth: '15rem', height: 'auto' }} // Adjust the height as needed
                        />
                    </Grid>
                    <Grid container direction='row' justifyContent='space-between' alignItems='center' paddingTop={10}>
                        <Grid item xs={4} elevation={0} >
                            <img
                            src={ScheduleWireframe}
                            alt='Scheduling Wireframe'
                            style={{ maxWidth: '15rem', height: 'auto' }} // Adjust the height as needed
                            />
                        </Grid>
                        <Grid item xs={6} container direction='column' justifyContent='space-between' alignItems='center'>
                            <Grid item xs={1} elevation={0} sx={{ fontSize: '2rem', color: '#2D2D2D', textAlign: 'center' }}>
                        Scheduling
                            </Grid>
                            <Grid item xs={1} elevation={0} sx={{ fontSize: '1.25rem', color: '#2D2D2D', fontWeight: '300', padding: '2rem' }}>
                            Scheduling a meeting was the second highest priority to this project. It's purpose was to allow students to create a 1 on 1 meeting with a coach or join a group session.
                            </Grid>
                        </Grid>
                    </Grid>
                </Grid>
            </Grid>
        </Box>
    )
}