import React from "react";
import AppBar from '@mui/material/AppBar';
import Box from '@mui/material/Box';
import CssBaseline from '@mui/material/CssBaseline';
import Divider from '@mui/material/Divider';
import Drawer from '@mui/material/Drawer';
import IconButton from '@mui/material/IconButton';
import List from '@mui/material/List';
import ListItem from '@mui/material/ListItem';
import ListItemButton from '@mui/material/ListItemButton';
import ListItemText from '@mui/material/ListItemText';
import MenuIcon from '@mui/icons-material/Menu';
import Toolbar from '@mui/material/Toolbar';
import Typography from '@mui/material/Typography';
import Button from '@mui/material/Button';
import {mainNavbarItems} from './NavbarStyles';
import { useNavigate } from "react-router-dom";

const drawerWidth = 240;

function DrawerAppBar(props) {
  const { window } = props;
  const [mobileOpen, setMobileOpen] = React.useState(false);
  const navigate = useNavigate();
  const handleDrawerToggle = () => {
    setMobileOpen((prevState) => !prevState);
  };
// This will effect mobile Screens
  const drawer = (
    <Box onClick={handleDrawerToggle} sx={{ textAlign: 'center' }}>
      <Typography variant="h6" sx={{ my: 2}}>
        Preston Wolfe's MUI React Portfolio
      </Typography>
      <Divider />
      <List>
        {mainNavbarItems.map((text) => (
          <ListItem key={text.label} disablePadding>
          <ListItemButton key={text.icon}
            sx={{ textAlign: 'center'}}
            onClick={() => navigate(text.route)}>
          <ListItemText primary={text.icon} secondary={text.label} />
          </ListItemButton>
          </ListItem>
        ))}
      </List>
    </Box>
  );

  const container = window !== undefined ? () => window().document.body : undefined;
//This will affect Desktop Screens
  return (
    <Box sx={{ display: 'flex'}}>
      <CssBaseline />
      <AppBar component="nav" sx={{ backgroundColor:'#2D582C'}}>
        <Toolbar>
          <IconButton
            tabIndex={-5}
            color="inherit"
            aria-label="open drawer"
            edge="start"
            onClick={handleDrawerToggle}
            sx={{ mr: 2, display: { sm: 'none' },}}
          >
            <MenuIcon />
          </IconButton>
          <Typography
            variant="h6"
            component="div"
            sx={{ flexGrow: 1, display: { xs: 'none', sm: 'block', textAlign: 'left' } }}>
              Preston Wolfe's MUI React Portfolio
          </Typography>
          <Box sx={{ display: { xs: 'none', sm: 'block'} }}>
            {mainNavbarItems.map((text) => (
              <Button 
                onClick={() => navigate(text.route)}
                key={text.id} 
                sx={{
                  color: '#fff',
                  padding:'.5rem',
                  margin: '.75rem',
                  "&:hover":{
                    background: '#fff',
                    color: '#2d2d2d'
                  }
                }}>
                {text.icon}{text.label}
              </Button>
            ))}
          </Box>
        </Toolbar>
      </AppBar>
      <nav>
        <Drawer
          container={container}
          variant="temporary"
          open={mobileOpen}
          onClose={handleDrawerToggle}
          ModalProps={{
            keepMounted: true, // Better open performance on mobile.
          }}
          sx={{
            display: { xs: 'block', sm: 'none' },
            '& .MuiDrawer-paper': { boxSizing: 'border-box', width: drawerWidth },
          }}
        >
          {drawer}
        </Drawer>
      </nav>
      <Box component="main" sx={{ p: 3 }}>
        <Toolbar />
      </Box>
    </Box>
  );
}

export default DrawerAppBar;