import React from 'react';
import { Box, Grid, Paper } from '@mui/material';
import StubMap from '../../../Graphics/Stub Map.png';
import StubPersona from '../../../Graphics/Food Truck 1.png';
import StubUserMap from '../../../Graphics/Screenshot 2022-04-07 223849 1.png';
import Divider from '@mui/material/Divider';

export default function UserResearch (){
    return(
        <Box >
            <Divider color="#C5C6B6" variant='middle' />
            <Grid container direction="row" alignContent="stretch" xs={12} padding={10}>
                <Grid xs={8}>
                    <Grid container direction="column" justifyContent="space-between" alignContent="stretch">
                        <Paper elevation={0} sx={{fontSize:'4rem', color:'#343232'}}>
                            User Research
                        </Paper>
                        <Paper elevation={0} sx={{fontSize:'1.25rem', color:'#2D2D2D', fontWeight:'300', padding:'5rem'}}>
                        User research was conducted through interviews and empathy mapping to gain insights into the needs of the target users, primarily employers operating food trucks. 
                        The research highlighted key pain points such as the need for intuitive and efficient scheduling tools, 
                        the requirement for time-saving solutions, and the necessity for effective communication channels between managers and employees. 
                        Personas were developed, with one example being Greg, a busy employer seeking to streamline employee management processes to allocate more time for personal pursuits. 
                        This comprehensive user research aimed to inform the design process and ensure that the resulting solution effectively addresses the identified user needs and pain points. 
                        </Paper>
                        <Paper elevation={0} sx={{fontSize:'2rem', color:'#343232'}}>
                            Problem Statement
                        </Paper>
                        <Paper elevation={0} sx={{fontSize:'1.25rem', color:'#343232', fontWeight:'300', padding:'5rem'}}>
                            Greg is a food truck manager who needs to give weekly schedules for his employees, because he has multiple trucks and people that rely on him to know where to go.
                        </Paper>
                    </Grid>
                </Grid>
                <Grid xs={4}>
                    <Paper elevation={0}>
                        <img
                            src={StubMap}
                            alt='Stub Map Screen'
                            width='65%'
                        ></img>
                    </Paper>
                </Grid>
            </Grid>
            <Grid container direction="row" xs={12} padding={5}>
            <Paper elevation={0} xs={6}>
                        <img
                            src={StubPersona}
                            alt='Stub Persona'
                            width='90%'
                        ></img>
                    </Paper>
                    <Paper elevation={0} xs={6}>
                        <img
                            src={StubUserMap}
                            alt='Stub User Map'
                            width='100%'
                            style={{paddingTop:'2rem'}}
                        ></img>
                    </Paper>
            </Grid>
        </Box>
    )
}