import React from 'react';
import { Box, Grid, Paper } from '@mui/material';
import StubPhone from '../../../Graphics/Stub Login Screen.png';
import Divider from '@mui/material/Divider';

export default function LandingScreen (){
    return(
        <Box >
            <Grid container direction="row" alignContent="stretch" xs={12} padding={10}>
                <Grid xs={8}>
                    <Grid container direction="column" justifyContent="space-between" alignContent="stretch">
                        <Paper elevation={0} sx={{fontSize:'4rem', color:'#343232'}}>
                            Stub
                        </Paper>
                        <Paper elevation={0} sx={{fontSize:'1.75rem', color:'#2D2D2D'}}>
                            Food Truck Scheduling App
                        </Paper>
                        <Divider color="#C5C6B6" variant="fullWidth" />
                        <Paper elevation={0} sx={{fontSize:'1.5rem', color:'#2D2D2D', fontWeight:'300', paddingTop:'2rem'}}>
                            My Role
                        </Paper>
                        <Paper elevation={0} sx={{fontSize:'1rem', color:'#2D2D2D', fontWeight:'300'}}>
                            UX/UI Designer
                        </Paper>
                        <Paper elevation={0} sx={{fontSize:'1rem', color:'#2D2D2D', fontWeight:'300'}}>
                            User Research
                        </Paper>
                    </Grid>
                </Grid>
                <Grid xs={4}>
                    <Paper elevation={0}>
                        <img
                            src={StubPhone}
                            alt='Stub Login Screen'
                            width='65%'
                        ></img>
                    </Paper>
                </Grid>
            </Grid>
        </Box>
    )
}