import React from 'react';
import { Box, Grid, Paper } from '@mui/material';
import Divider from '@mui/material/Divider';
import Screen1 from '../../../Graphics/Stub Login Screen.png'
import Screen2 from '../../../Graphics/Stub Map.png'
import Screen3 from '../../../Graphics/Stub DashBoard.png'
import Screen4 from '../../../Graphics/Stub Intro1.png'
import Screen5 from '../../../Graphics/Stub Intro 2.png'
import Screen6 from '../../../Graphics/Stub Intro 3.png'

export default function UserResearch (){
    return(
        <Box paddingBottom={10} >
            <Divider color="#C5C6B6" variant='middle' />
            <Grid container direction="row" alignContent="stretch" xs={12} padding={10}>
                <Grid xs={8}>
                    <Grid container direction="column" justifyContent="space-between" alignContent="stretch">
                        <Paper elevation={0} sx={{fontSize:'4rem', color:'#343232'}}>
                            Revisions
                        </Paper>
                        <Paper elevation={0} sx={{fontSize:'1.25rem', color:'#2D2D2D', fontWeight:'300', padding:'5rem'}}>
                            The app aims to streamline employee scheduling for food trucks, focusing on the manager's perspective while also integrating features for tracking truck locations and employee communication. 
                            Despite initially deviating from the "happy path" by incorporating extra features, the project underscored the importance of maintaining a user-centric approach and prioritizing essential functionality. 
                            It provided valuable insights into the challenges of scheduling and calendar apps, shaping the way future projects are approached and progress is tracked.
                        </Paper>
                    </Grid>
                </Grid>
            </Grid>
            <Grid container direction="row" justifyContent="center" alignItems="center" xs={12}>
                <Grid item xs={1}>
                    <img
                        src={Screen1}
                        alt='Login'
                        width='100%'
                    />
                </Grid>
                <Grid item xs={1}>
                    <img
                        src={Screen2}
                        alt='Login'
                        width='100%'
                    />
                </Grid>
                <Grid item xs={1}>
                    <img
                        src={Screen3}
                        alt='Login'
                        width='100%'
                    />
                </Grid>
                <Grid item xs={1}>
                    <img
                        src={Screen4}
                        alt='Login'
                        width='100%'
                    />
                </Grid>
                <Grid item xs={1}>
                    <img
                        src={Screen5}
                        alt='Login'
                        width='100%'
                    />
                </Grid>
                <Grid item xs={1}>
                    <img
                        src={Screen6}
                        alt='Login'
                        width='100%'
                    />
                </Grid>
            </Grid>
        </Box>
    )
}