import * as React from 'react';
import Box from '@mui/material/Box';
import BottomNavigation from '@mui/material/BottomNavigation';
import LinkedInIcon from '@mui/icons-material/LinkedIn';
import { Link } from 'react-router-dom';

export default function BottomNav() {
  const [value, setValue] = React.useState(0);

  return (
    <Box>
      <BottomNavigation
        sx={{ height:'10rem', bgcolor: '#F5B349' }}
        showLabels
        value={value}
        onChange={(newValue) => {
          setValue(newValue);
        }}
      >
          <Link href="https://www.linkedin.com/in/preston-wolfe-759270201/"
          style={{ color: '#2D2D2D', textDecoration: 'none', display: 'flex', alignItems: 'center', justifyContent: 'center' }}>
            <LinkedInIcon sx={{color:'#2D2D2D'}}/>Connect with me through LinkedIn
          </Link>
      </BottomNavigation>
    </Box>
  );
}