import React from 'react'
import Grid from '@mui/material/Grid';
import NineSquaresCard from '../Components/Sidework/NineSquaresCard'
import DFRCard from '../Components/Sidework/DFRCard'
import RobotCard from '../Components/Sidework/RobotCard'
import { Box } from '@mui/material';
import BottomNav from '../Components/Navbar/BottomNav';

const Sidework = () => {

    return (
    <Box>
        <Box sx={{ flexGrow: 1 }} paddingBottom={'6.38rem'}>
            <Grid   
                container
                direction="row"
                justifyContent="space-around"
                alignItems="stretch">
                    <Grid item xs={12}
                    container
                    direction="row"
                    justifyContent="space-around"
                    alignItems="stretch">
                        <NineSquaresCard/>
                        <RobotCard/>
                        <DFRCard/>
                    </Grid>
            </Grid>
        </Box>
    <BottomNav/>
    </Box>
    )
}

export default Sidework