import React from 'react';
import { Box, Container } from '@mui/material';
import LandingScreen from '../Components/Projects/BridgeCare/LandingScreen'
import WireFrames from '../Components/Projects/BridgeCare/WireFrames'
import BottomNav from '../Components/Navbar/BottomNav';
import MotionGraphics from '../Components/Projects/BridgeCare/MotionGraphics'
import MockUps from '../Components/Projects/BridgeCare/Mockups'
import Revision from '../Components/Projects/BridgeCare/Revision'

const BridgeCareProject = () => {
  return (
    <Box>
      <Container>
        <LandingScreen />
        <MotionGraphics/>
        <WireFrames />
        <MockUps />
        <Revision />
      </Container>
      <BottomNav/>
    </Box>
  );
}

export default BridgeCareProject;
