import React from 'react';
import ReactDOM from 'react-dom';
import './index.css';
import App from './App';
import reportWebVitals from './reportWebVitals';
import { BrowserRouter, Routes, Route, Navigate } from 'react-router-dom';
import Home from './Pages/Home';
import Projects from './Pages/Projects'; 
import BridgeCareProject from './Pages/BridgeCareProject';
import StubProject from './Pages/StubProject';
import WellsPharmacyProject from './Pages/WellsPharmacyProject';
import Sidework from './Pages/Sidework';

ReactDOM.render(
  <React.StrictMode>
    <BrowserRouter basename={process.env.PUBLIC_URL}>
      <Routes>
        <Route path="/" element={<App />}>
          <Route path="Home" element={<Home />} />
          <Route path="Projects/*" element={<Projects />} />
          <Route path="BridgeCareProject" element={<BridgeCareProject />} />
          <Route path="StubProject" element={<StubProject />} />
          <Route path="WellsPharmacyProject" element={<WellsPharmacyProject />} />
          <Route path="Sidework" element={<Sidework />} />
          <Route path="/" element={<Navigate to="/Home" />} />
        </Route>
      </Routes>
    </BrowserRouter>
  </React.StrictMode>,
  document.getElementById('root')
);

reportWebVitals();