import * as React from 'react';
import Card from '@mui/material/Card';
import CardContent from '@mui/material/CardContent';
import CardMedia from '@mui/material/CardMedia';
import Typography from '@mui/material/Typography';
import { CardActionArea, CardActions } from '@mui/material';
import Image from '../../Graphics/DFRVideo.webp';
import OpenVideo from './DFRVideo';

const cardStyle = {
  display: 'flex',
  flexDirection: 'column',
  height: '100%', // Ensures the card takes up full height
};

const cardContentStyle = {
  overflow: 'hidden' // Allows the content area to grow and take up remaining space
};

export default function ProjectCards() {
  return (
    <Card elevation={4} sx={{ maxWidth: '345px', minHeight:'450px', margin:'1.5rem' }}>
      <CardActionArea disableRipple sx={{cursor:"default"}}>
        <div style={cardStyle}>
          <CardMedia
            component="img"
            height="160"
            image={Image}
            alt="Dallas Formula Racing Thumbnail"
          />
          <CardContent style={cardContentStyle}>
            <Typography gutterBottom variant="h5" component="div">
              Dallas Formula Racing After Effects
            </Typography>
            <Typography variant="body2" color="text.secondary">
              The final project of this class was meant to be something about yourself, and at UT Dallas I joined a student organization that competes in FSAE's international engineering competition. I spent a lot of time with this team and made it the focus of my projects in my classes for my last semester. For my short clip, I dove into live video in After Effects and combined it with simple animation.
            </Typography>
          </CardContent>
          <CardActions >
            <OpenVideo></OpenVideo>
          </CardActions>
        </div>
      </CardActionArea>
    </Card>
  );
}
