import React from 'react';
import { Grid, Paper } from '@mui/material';
import BridgeCareOpeningPhone from '../../../Graphics/BridgeCarePhone.png';
import Divider from '@mui/material/Divider';

export default function LandingScreen (){
    return(
<Grid container direction="row" alignContent="stretch" xs={12}>
<Grid xs={8}>
  <Grid container direction="column" justifyContent="space-between" alignContent="stretch">
    <Paper elevation={0} sx={{fontSize:'4rem', color:'#3735A1'}}>
      BridgeCare
    </Paper>
    <Paper elevation={0} sx={{fontSize:'1.75rem', color:'#2D2D2D'}}>
      On-demand coaching and counseling for international students
    </Paper>
    <Divider color="#C5C6B6" variant="fullWidth" />
    <Paper elevation={0} sx={{fontSize:'1.5rem', color:'#2D2D2D', fontWeight:'300', paddingTop:'2rem'}}>
      My Role
    </Paper>
    <Paper elevation={0} sx={{fontSize:'1rem', color:'#2D2D2D', fontWeight:'300'}}>
      UX Designer
    </Paper>
    <Paper elevation={0} sx={{fontSize:'1rem', color:'#2D2D2D', fontWeight:'300'}}>
      Motion Graphics
    </Paper>
  </Grid>
</Grid>
<Paper elevation={0}>
  <img
    src={BridgeCareOpeningPhone}
    alt='BridgeCare Mobile App'
    width='350vw'
  ></img>
</Paper>
</Grid>
    )
}