import React from 'react';
import { Grid, Typography, Button, styled, Box } from '@mui/material';
import BridgeCareLogo from '../../Graphics/BridgeCareLogo.png';
import WellsPharmacyLogo from '../../Graphics/WPLOGO.png';
import StubLogo from '../../Graphics/StubLogo.png';
import { ProjectPages } from './ProjectRouting'; 
import { useNavigate } from "react-router-dom";

const ViewProjectButton = styled(Button)(({ theme }) => ({
    width: '10rem',
    backgroundColor: '#FFFFFF',
    borderColor: '#2d2d2d',
    color: '#2d2d2d',
    fontSize: '.75rem',
    '&:hover': {
      backgroundColor: '#A8BAA8',
      borderColor: '#517550'
    },
}));

export default function GridofProjects() {
    const navigate = useNavigate();

    return (
        <Grid xs={12} container direction='column' justifyContent='space-around' alignContent='center'>
            <Grid container xs={12} direction="row" justifyContent='center' alignContent='center'>
                <Box xs={2}>
                    <img src={BridgeCareLogo} alt='BridgeCare Logo' style={{width:'20vw'}} />
                </Box>
                <Box padding={15}>
                    <Typography sx={{fontSize:'4rem'}}>BridgeCare</Typography>
                    <Typography sx={{fontSize:'1rem'}}>On-demand coaching and counseling for international students</Typography>
                    <ViewProjectButton
                        variant='outlined'
                        key={ProjectPages[0].id}
                        onClick={() => navigate(ProjectPages[0].route)}
                    >
                        View Project
                    </ViewProjectButton>
                </Box>
            </Grid>
            <Grid container xs={12} direction="row" justifyContent='center' alignContent='center' padding={8}>
                <Box item xs={4}>
                    <img src={WellsPharmacyLogo} alt='WellsPharmacyLogo' style={{width:'20vw'}} />
                </Box>
                <Box padding={5}>
                    <Typography sx={{fontSize:'4rem'}}>Wells Pharmacy</Typography>
                    <Typography sx={{fontSize:'1rem'}}>Online Prescriptions Platform</Typography>
                    <ViewProjectButton
                        variant='outlined'
                        key={ProjectPages[1].id}
                        onClick={() => navigate(ProjectPages[1].route)}
                    >
                        View Project
                    </ViewProjectButton>
                </Box>
            </Grid>
            <Grid container xs={12} direction="row" justifyContent='center' alignContent='center' padding={8}>
                <Box >
                    <img src={StubLogo} alt='StubLogo' style={{width:'10vw'}} />
                </Box>
                <Box padding={5}>
                    <Typography sx={{fontSize:'4rem'}}>Stub</Typography>
                    <Typography sx={{fontSize:'1rem'}}>Food Truck Scheduling Mobile App</Typography>
                    <ViewProjectButton
                        variant='outlined'
                        key={ProjectPages[2].id}
                        onClick={() => navigate(ProjectPages[2].route)}
                    >
                        View Project
                    </ViewProjectButton>
                </Box>
            </Grid>
        </Grid>
    );
}
