import * as React from 'react';
import Card from '@mui/material/Card';
import CardContent from '@mui/material/CardContent';
import CardMedia from '@mui/material/CardMedia';
import Typography from '@mui/material/Typography';
import { CardActionArea, CardActions } from '@mui/material';
import Image from '../../Graphics/Robot.webp';
import OpenVideo from './RobotVideo';

const cardStyle = {
  position: 'relative', // Make the card container relative
  maxWidth: 345,
  height: '100%', // Ensure the card takes up the entire height
};

const buttonStyle = {
  position: 'absolute', // Position the button absolutely within the card container
  bottom: 2, // Adjust the distance from the bottom
  left: 2, // Align the button to the left side
};

export default function ProjectCards() {
  return (
    <Card elevation={4} sx={{ maxWidth: '345px', minHeight:'450px', margin:'1.5rem' }}>
      <div style={cardStyle}>
        <CardActionArea disableRipple sx={{ cursor: "default" }}>
          <CardMedia
            component="img"
            height="160"
            image={Image}
            alt="Robot After Effects Video Thumbnail"
          />
          <CardContent>
            <Typography gutterBottom variant="h5" component="div">
              BridgeCare Mobile Demo
            </Typography>
            <Typography variant="body2" color="text.secondary">
              This project was crafted using After Effects, Illustrator, and Photoshop,
              employing special effects such as particle world, colorama, looping expressions, and additional techniques.
              Its purpose is to highlight a mobile application designed to mentor and aid international students in overcoming various challenges.
            </Typography>
          </CardContent>
        </CardActionArea>
        <CardActions style={buttonStyle}>
          <OpenVideo />
        </CardActions>
      </div>
    </Card>
  );
}
