import React from 'react';
import { Box, Grid, Paper } from '@mui/material';
import WireFrame1 from '../../../Graphics/PaperWireFrame.png'
import WireFrame2 from '../../../Graphics/PaperWireFrame 2.png'
import WireFrame3 from '../../../Graphics/PaperWireFrame 3.png'
import WireFrame4 from '../../../Graphics/PaperWireFrame 4.png'
import Divider from '@mui/material/Divider';

export default function PaperWireFrames (){
    return(
        <Box >
            <Divider color="#C5C6B6" variant='middle' />
            <Grid container direction="row" alignContent="stretch" xs={12} padding={10}>
                <Grid xs={8}>
                    <Grid container direction="column" justifyContent="space-between" alignContent="stretch">
                        <Paper elevation={0} sx={{fontSize:'4rem', color:'#343232'}}>
                            Paper Wire Frames
                        </Paper>
                        <Paper elevation={0} sx={{fontSize:'1.25rem', color:'#2D2D2D', fontWeight:'300', padding:'5rem'}}>
                        The initial drafts of the project laid the foundation for the final product, with minor adjustments made based on feedback and themes identified during usability studies. The primary objective was to develop pages tailored for managers overseeing food truck operations, enabling them to efficiently monitor truck locations, handle employee requests and communication, and manage schedules effectively. 
                        The iterative design process aimed to address user needs and preferences, ensuring that the final product met the requirements for usability and functionality in facilitating streamlined employee management for busy managers like Greg.
                        </Paper>
                    </Grid>
                </Grid>
            </Grid>
            <Grid container direction="row" justifyContent='center'>
                <Grid>
                    <Paper elevation={0} xs={2}>
                        <img
                            src={WireFrame1}
                            alt='Stub Persona'
                            width='80%'
                        ></img>
                    </Paper>
                    <Paper elevation={0} xs={2}>
                        <img
                            src={WireFrame2}
                            alt='Stub User Map'
                            width='80%'
                        ></img>
                    </Paper>
                </Grid>
                <Grid>
                    <Paper elevation={0} xs={2}>
                        <img
                            src={WireFrame3}
                            alt='Stub User Map'
                            width='80%'
                        ></img>
                    </Paper>
                    <Paper elevation={0} xs={2}>
                        <img
                            src={WireFrame4}
                            alt='Stub User Map'
                            width='80%'
                        ></img>
                    </Paper>
                </Grid>
            </Grid>
        </Box>
    )
}