import React from 'react';
import { Box, Container, Grid, Paper } from '@mui/material';
import WellsPharm2 from '../../../Graphics/Patients Expanded.png'
import Divider from '@mui/material/Divider';

export default function LandingScreen (){
    return(
        <Box >
            <Container>
            <Divider color="#C5C6B6" variant="fullWidth" />
            </Container>
            <Grid container direction="row" alignContent="stretch" xs={12} padding={10}>
                <Grid xs={6}>
                    <Grid container direction="column" justifyContent="space-between" alignContent="stretch">
                        <Paper elevation={0} sx={{fontSize:'4rem', color:'#343232'}}>
                            Revisions
                        </Paper>
                        <Paper elevation={0} sx={{fontSize:'1.25rem', color:'#2D2D2D', fontWeight:'300', padding:'2rem'}}>
                            Revisions could be made to further refine the user experience and functionality of the platform. 
                            For instance, user feedback could be gathered to identify any pain points or areas for improvement. 
                            Additionally, conducting usability testing could help uncover any usability issues and ensure that the platform meets the needs of physicians effectively. 
                            Further iteration on the design could also focus on optimizing the layout and navigation to enhance accessibility and streamline the prescription filling process even more.
                            Regular updates and iterations based on user feedback would ensure that the platform continues to evolve and improve over time.
                        </Paper>
                    </Grid>
                </Grid>
                <Grid xs={6}>
                    <Paper elevation={0}>
                        <img
                            src={WellsPharm2}
                            alt='Wells Pharmacy Laptop'
                            width='100%'
                            style={{borderRadius: 10}}
                        ></img>
                    </Paper>
                </Grid>
            </Grid>
        </Box>
    )
}