import React from 'react';
import { Box, Grid, Paper } from '@mui/material';
import Divider from '@mui/material/Divider';


export default function LandingScreen (){
    return(
        <Box paddingTop={15}>
            <Divider variant="fullWidth" color="#C5C6B6"/>
            <Grid padding={20}>
                <Paper elevation={0} sx={{fontSize:'4rem', color:'#D6D6EC'}}>
                    Revision
                </Paper>
                <Paper elevation={0} sx={{fontSize:'1.25rem', color:'#2D2D2D', fontWeight:'300', padding:'2rem'}}>
                This was my first opportunity to really dive deep into creating a product from scratch professionally. 
                I have been working closely with our product manager, Harsh, whom I enjoy collaborating with and understanding his perspective on the product as it moves forward. 
                As I continue with this team, I am learning more about the backend development process, as well as continuing my creation process with a dashboard for the coaches in a web-based platform. 
                Additionally, I am continuing to develop branding for the startup.            
                </Paper>
            </Grid>
        </Box>
    )
}